* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
header{
    margin-bottom: 25px;
}
.menu-bg {
    background: #b3124d;
    width: 100%;
    /*height: 10vh;*/
    position: relative;
}

.logo {
    width: 180px;
    height: 55px;
    padding: 5px 0px;
}

.hambarger-menu {
    position: absolute;
    top: 1.6em;
    right: .8em;
}

    .hambarger-menu span {
        width: 22px;
        height: 3px;
        background: white;
        border-radius: 5px;
        display: block;
        margin-bottom: 3px;
    }

.site-nagivation {
    display: none;
    font-size: 20px;
}
.show-nav {
    display: block;
}

.site-nagivation ul {
    list-style: none;
}

.site-nagivation ul li {
    margin: 5px 0px;
}

.site-nagivation ul li a {
    text-decoration: none;
    color: white;
}

.li-level-2 {
    display: flex;
    flex-direction: column;
}

.ul-level-2 {
    padding-left: 22px;
    display: none;
}
.show-link{
    display: block;
}

.fa {
    color: white;
    font-size: 13px;
    margin-left: 15px;
}

main {
    padding: 15px 5px;
}

.entry-title {
    font-weight: bold;
}

article {
    /*color: #8c1d46; */
    text-align: justify;
}

.header-items {
    display: flex;
    flex-direction: column;
}
